  import { Card, CardBody, CardHeader } from "react-bootstrap";
  import { baseUrl, hideLangFilter } from "../../../../../utils";
  import React, { useEffect, useState } from "react";
  import AssetsbySkeleton from "../../layout/skeleton/assetsbySkeleton";
  import NoData from "../../layout/common/noData";
import { shallowEqual, useSelector } from "react-redux";

  const TopAiTags = (props) => {
    const { topaiTags, top_ai_loader } =props

    const { analyticwall } = useSelector(
      ({ analyticwall }) => ({
        analyticwall: analyticwall.selectedWallFeed.feeds,
      }),
      shallowEqual
    );

    const [gettagList ,setgettagList] = useState([])

      const tagUpdate=()=>{

          const feedList = analyticwall && Object.keys(analyticwall).length > 0 ? analyticwall.map(item => item.id) : [];
          const tagList = analyticwall && Object.keys(topaiTags).length > 0 ? topaiTags.map(item => item.feedId) : [];
          const tagListData = [...new Map(tagList.map(item => [item, item])).values()].join(", ");

          let getTags = topaiTags;
              if(getTags && getTags.length > 0){
                getTags = getTags.filter((lang) => {
                    if(hideLangFilter(feedList, tagListData,1)) return lang;
              });

              getTags.splice(6)
              setgettagList(getTags)
          }
     }
 

  useEffect(() => {
     gettagList && analyticwall && Object.keys(gettagList).length && tagUpdate()
  }, []);
    

    return (
      <Card className="sw-card-custom gutter-b mb-5 card-shadow">
        <CardHeader className="p-5">
          <h6 class="card-label font-weight-bolder m-0">
            <img
              src={baseUrl(`dashboard/analytic/ai.svg`)}
              height={"20px"}
              width={"20px"}
            />{" "}
            Top AI Tags
          </h6>
        </CardHeader>
        <CardBody className="p-5">

         { top_ai_loader ? <AssetsbySkeleton/> : gettagList && Object.keys(gettagList).length ?
           gettagList.map((data,index)=>{
            return(<div className="px-5 py-2 border rounded mb-4 last-child-mb-0">
              <div className="d-flex align-items-center justify-content-between">
                <div className="d-flex align-items-center me-10">
                  <div className="text-dark font-weight-boldest me-2">{index+1}.</div>
                  <div className="">
                    <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                       {data.tag}
                    </p>
                  </div>
                </div>
                <p className="count-tag border font-weight-boldest m-0">{data.count}</p>
              </div>
            </div>)
        }) :         
            <NoData/>
        }
        </CardBody>
      </Card>
    );
  };

 export default React.memo(TopAiTags);
